.box {
    display: flex;
    align-items: center;
    background-repeat: no-repeat;
    color: var(--color-text);
}

.box__pic {
    flex: 4;
}

.picBox {
    justify-items: center;
    display: flex;
    justify-content: center;
}

.picName {
    display: flex;
    justify-content: center;
    font-size: 25px;
    font-weight: lighter;
    padding-top: 2rem;
}

.pic {
    height: 50vh;
    border-radius: 40%;
}

.box__text {
    flex: 6;
    align-items: center;
    justify-items: center;
    overflow: hidden;
}



.slider {
    display: grid;
    grid-template-rows: 1fr 1fr;
    
}

.sliderItems {
    height: 50px;
    overflow: hidden;
    align-items: center;
    justify-self: center;
}


.please {
    height: 50px;
    font-size: 2.5rem;
    display: flex;
    align-items: center;
}
@keyframes move {
    50% {
        transform: translateY(-50px);
    }
    100% {
        transform: translateY(-100px);
    }
    
}

.please {
    animation: move 10s ease-in-out infinite alternate;
}

.dev-text {
    grid-row-start: 2;
    max-width: 500px;
    justify-self: center;
    padding-top: 30px;

}


/*=================== MEDIA QUERIES (Medium devices) ===================*/
@media screen and (max-width: 1024px) {
    .box {
        flex-direction: column;
        gap: 2rem;
        padding-top: 5rem;
        height: fit-content !important;
    }


}

/*=================== MEDIA QUERIES (Small devices) ===================*/
@media screen and (max-width: 600px) {
    .box {
        flex-direction: column;
        height: fit-content !important;
        margin: auto;
        width: var(--container-width-s);
        justify-content: space-between;
        margin-bottom: 20px;
        padding-left: 10px;
    }

    .dev-text {
        padding-left: 20px;
    }

    .picName {
        padding-left: 20px;
    }

    
}