nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1rem;
    border-radius: 3rem;
    padding-inline: 1rem;
}

a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    transition: var(--transition);
}

a:hover {
    background: rgba(0, 0, 0, 0.4);
    color: var(--color-bg) !important;
}

.active {
    background: var(--color-shadow);
}