#contact {
    display: grid;
    grid-template-rows: 1fr 6fr;
}

.sectionTop {
    grid-row-start: 1;
    justify-self: center;
    align-self: center;
}

.contactContainer {
    grid-row-start: 2;
    display: flex;
    padding: 0 150px 0 150px;
    justify-content: space-between;
    align-items: start;
    margin: 50px;
    padding-bottom: 50px;
    gap: 5rem;
}

.contactOptions {
    flex:1;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.option {
    background: white;
    color: var(--color-text);
    padding: 1.2rem;
    border-radius: 2rem;
    text-align: center;
    border: 1px solid transparent;
    box-shadow: 1px 1px 10px black;
}

.option:hover {
    color: white;
    background-color: var(--color-box);
    transition: var(--transition);
}

.option:hover a {
    color: white;
}


.icon {
    font-size: 1.5rem;
    justify-self: center;
}

.linker {
    display: inline-block;
    
    
}


.form {
    flex: 3;
    display: flex;
    flex-direction: column;
}

.geng {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid gray;
    resize: none;
    font-size: 15px;
}




/*=================== MEDIA QUERIES (Medium devices) ===================*/
@media screen and (max-width: 1024px) {
    .contact {
        align-items: start;
        height: fit-content;
        margin-bottom: 100px;
        padding: 0;
    }

    .contactContainer {
        margin-top: 50px;
        flex-direction: row;
        width: var(--container-width-m);
        justify-self: center;
        padding-bottom: 0;
    }

    .geng {
        display: flex;
        flex-direction: column;
        row-gap: 2rem;
    }
}
/*=================== MEDIA QUERIES (Small devices) ===================*/
@media screen and (max-width: 600px) {
    .contactContainer {
        margin-top: 1rem;
        flex-direction: column;
        width: var(--container-width-s);
        overflow-x: hidden;
        justify-self: right;
    }

    .contactOptions {
        display: none;
    }

    .geng {
        display: flex;
        flex-direction: column;
        row-gap: 1rem;
        padding-bottom: 10px;
    }

    
}