#about {
    display: grid;
    align-items: center;
    justify-items: center;
    height: 100vh;
    grid-template-rows: auto;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
    "header header"
    "main cards"
    "main empty";
    color: var(--color-text);
}

.topic {
    grid-area: header;
    padding: 1rem;
}

.about__me {
    width: fit-content;
    height: fit-content;
    grid-area: main;
    display: flex;
    align-self: flex-start;
    background: linear-gradient(
        45deg,
        transparent,
        rgb(185, 190, 189)
    );
}
.about__me-image {
    transform: rotate(10deg);
    background: linear-gradient(
        45deg,
        transparent,
        rgb(5, 54, 44)
    );
}

.about__me-image:hover {
    transform: rotate(0deg);
    transition: var(--transition);
}

.about__content {
    grid-area: cards;
    align-items: start;
    padding-right: 1rem;
}
.about__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 1rem;
}

.about__card {
    display: grid;
    background: white;
    font-size: 15px;
    box-shadow: 1px 1px 10px rgb(124, 124, 124);
    border-radius: 1rem;
    border: 1px solid transparent;
    padding: 1rem;
    font-weight: 500;
    justify-items: center;
    grid-template-areas: 
    "award"
    "short" 
    "long";
    gap: 1rem;
}

.about__card:hover {
    background: var(--color-box);
    color: white;
    transition: var(--transition);
}

img {
    height: 50vh;
    width: auto;
}

FaAward {
    grid-area: award;
}

h5 {
    grid-area: short;
}

small {
    grid-area: long;
}

.bottom-text {
    grid-area: empty;
    align-self: start;
    justify-self: center;
    padding: 4rem;
}

p {
    margin-bottom: 2rem;
}

.btn {
    height: fit-content;
    border-radius: 0.5rem;
    color: var(--color-text);
    border-color: transparent;
    font-size: 15px;
    box-shadow: 1px 1px 10px black;
}

.btn:hover {
    color: white;
    background-color: var(--color-box);
    transition: var(--transition);
}

    
#linker {
    display: inline-block;
    background: transparent !important;
}



/*=================== MEDIA QUERIES (Medium devices) ===================*/
@media screen and (max-width: 1024px) {
    #about {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        padding-top: 5rem;
        justify-content: center;
        height: fit-content !important;
    }

    .about__me {
        display: none;
    }

    .bottom-text {
        display: flex;
        flex-direction: column;
    }

}

/*=================== MEDIA QUERIES (Small devices) ===================*/
@media screen and (max-width: 600px) {
   .about {
    width: var(--container-width-s);
    justify-content: center;
    margin: auto;
   }

   .about__cards {
       margin-left: 10px;
   }
}