#services {
    display: grid;
    grid-template-rows: 1fr 6fr;
}

.sectionTop {
    grid-row-start: 1;
    justify-self: center;
    align-self: center;
    color: var(--color-text);
}

.carded {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    padding: 80px;
}

.carded > div {
    background: white;
    padding: 2.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    box-shadow: 1px 1px 10px black;
    display: flex;
    flex-direction: column;
}



.carded > div:hover {
    color: white;
    background-color: var(--color-box);
    transition: var(--transition);
}

.carded > div:hover .details {
    color: white;
}
.content {
    display: grid;
    gap: 1rem;
    row-gap: 1rem;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    
}

.text {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-text);
    font-weight: bold;
}

.carded > div:hover .text {
    color: white;
}

.details {
    color: var(--color-text);
    width: fit-content;
    margin-left: 20px;
}

/*=================== MEDIA QUERIES (Medium devices) ===================*/
@media screen and (max-width: 1024px) {
    #services {
        height: fit-content;
    }
    .carded {
        grid-template-columns: 1fr;
    }

    .carded > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    
    .content {
        padding: 1rem;
    }

}

/*=================== MEDIA QUERIES (Small devices) ===================*/
@media screen and (max-width: 600px) {
    .serv {
        width: var(--container-width-s);
        margin: auto;

    }

    .carded {
        gap: 1rem;
        justify-content: center;
        overflow-x: hidden;
    }

    .carded > div {
        
        padding: 2rem 2rem;
        
    }
    
}